const imgExtnames = [
    "jpg", "jpeg",
    "png", "webp",
    "bmp", "svg",
    "gif", "psd",
]
export default {
    methods: {
        //  input
        imgRevClear() {
            const imgRev = this.$refs.imgRev
            imgRev.value = ""
        },
        // 
        imgRevFunc() {
            const imgRev = this.$refs.imgRev
            const file = imgRev.files[0]
            const fileType = file.name.split(".")[1] // 
            if (!imgExtnames.includes(fileType)) {
                // 
                alert("")
                this.imgRevClear()
            }
        }
    }
}